.card {
  display: flex;
  margin: 20px 100px;
  box-shadow: 0 2px 4px black;
  align-items: center;
}
img {
  height: 100px;
  width: 100px;
}
h1 {
  margin: 0 20px;
}

.nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
}
.nav > input{
    height: 20px;
}
.nav-button {
  text-decoration: none;
  border: 1px solid gray;
  height: 30px;
  padding: 0.5rem;
  text-align: center;
  border-radius: 10px;
}
